header {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  height: 50px;
  line-height: 50px;
  font-size: 0;
  z-index: 10;
  position: relative;
}

header #menu-container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  position: relative;
}

header #menu-container .menu-item {
  font-size: 16px;
  display: inline-block;
  background-color: #ededed;
  height: 50px;
  text-align: center;
}

header #menu-container .menu-item-logo {
  width: 20%;
  background-color: #ededed;
  vertical-align: top;
}

header #menu-container .right-container {
  display: inline-block;
  position: relative;
  width: 80%;
  font-size: 0;
}

header #menu-container .right-container .menu-item-icon {
  display: inline-block;
  height: 50px;
  width: calc(100%/3);
  font-size: 20px;
  vertical-align: top;
}

header #menu-container .right-container .menu-item-icon span {
  display: none;
}

header #menu-container .right-container .menu-item-icon:hover {
  background-color: #A19B88;
  cursor: pointer;
}

header #menu-container .menu-item-nav {
  position: relative;
  top: 15px;
  height: 22px;
}

header #menu-container .menu-item-nav div {
  margin: auto;
  padding: 0;
  display: block;
  position: relative;
  width: 35px;
  height: 4px;
  background-color: black;
}

header #menu-container .menu-item-nav div:nth-child(2) {
  top: 5px;
}

header #menu-container .menu-item-nav div:nth-child(3) {
  top: 10px;
}

header #menu-container .search-bar {
  display: inline-block;
  text-align: center;
  width: 100%;
  margin: auto;
  height: 50px;
  box-sizing: border-box;
  padding: 5px;
  vertical-align: middle;
}

header .cart-price {
  font-size: 0.8em;
}

@media (min-width: 800px) {
  #menu-container .right-container .menu-item-icon span {
    display: inline !important;
    font-size: 0.9em;
  }
}

@media (min-width: 1200px) {
  header #menu-container .right-container {
    width: 40%;
    position: absolute;
    right: 0;
  }
  header #menu-container .right-container .menu-item-icon {
    width: 50%;
  }
  header #menu-container .menu-item-icon-nav {
    display: none !important;
  }
  header #menu-container .search-bar {
    position: relative;
    width: 40%;
    background-color: #ededed;
  }
}

nav {
  display: block;
  width: 200px;
  background-color: #A19B88;
  text-align: center;
  position: absolute;
  top: 0;
  right: -200px;
  z-index: 100;
  font-size: 15px;
  height: 100%;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 50px 0;
}

nav ul li {
  text-transform: uppercase;
  display: block;
  line-height: 50px;
  text-align: center;
  border-bottom: 1px solid #ededed;
}

nav ul li:first-child {
  border-top: 1px solid #ededed;
}

nav ul li:hover {
  cursor: pointer;
  background-color: #908A77;
}

nav ul li:hover .subcat {
  display: block;
}

nav ul li .subcat {
  display: none;
  list-style-type: none;
  padding: 0;
  background-color: #908A77;
}

nav ul li .subcat li {
  text-transform: none;
}

nav ul li .subcat li:hover {
  background-color: #807966;
}

.nav-transiotion {
  transition: right 0.2s linear;
}

@media (min-width: 1200px) {
  nav {
    margin: auto;
    position: relative;
    right: 0;
    width: 1200px !important;
    height: 50px !important;
    display: block;
    z-index: 1 !important;
  }
  nav ul {
    padding: 0;
  }
  nav ul li {
    display: inline-block;
    vertical-align: top;
    width: 150px;
    border: none;
  }
  nav ul li:first-child {
    border-top: none;
  }
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Dosis', sans-serif;
  font-size: 16px;
}

body a {
  text-decoration: none;
}

body a:visited {
  color: inherit;
}

.container {
  height: 100%;
  position: relative;
  left: 0;
  transition: left 0.2s linear;
  z-index: 0;
}

.container-shift {
  left: -200px !important;
}

#content {
  position: relative;
  margin-top: 50px;
  padding: 20px 0;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  z-index: 0;
}

#content .product-list {
  text-align: center;
  margin: auto;
  z-index: 0;
}

#content .product-list .product {
  display: inline-block;
  position: relative;
  text-align: center;
  width: 250px;
  height: 315px;
  margin: 10px 10px;
  vertical-align: top;
  border: 1px solid #ededed;
}

#content .product-list .product * {
  box-sizing: border-box;
}

#content .product-list .product h2, #content .product-list .product h3 {
  margin: 5px;
}

#content .product-list .product img {
  width: 250px;
  height: 250px;
  margin: 0;
}

#content .product-list .product a {
  color: #3E3F41;
  display: inline-block;
  width: 100%;
  margin: 0;
}

#content .product-list .product .product-image {
  height: 250px;
}

#content .product-list .product .product-info {
  height: 60px;
  background-color: #ededed;
  margin: 0;
  color: #3E3F41;
}

#content .product-list .product:hover {
  cursor: pointer;
}

#content .product-container {
  text-align: center;
}

#content .product-container .product-image {
  display: inline-block;
  width: 95%;
}

#content .product-container .product-image img {
  width: 100%;
}

#content .product-container .product-param {
  padding: 0 10px;
  width: 95%;
  height: 100%;
  display: inline-block;
  border-bottom: 1px solid #ededed;
}

#content .product-container .product-param h1 {
  margin-top: 5px;
  margin-bottom: 10px;
  font-size: 3em;
  text-transform: uppercase;
}

#content .product-container .product-param .brand-name {
  font-size: 1.5em;
  margin-bottom: 15px;
}

#content .product-container .product-param .product-availability {
  padding-bottom: 15px;
  margin-bottom: 15px;
  width: 100%;
  border-bottom: 1px solid #ededed;
}

#content .product-container .product-param .product-price {
  font-size: 1.5em !important;
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #ededed;
}

#content .product-container .product-param .product-price span {
  font-size: 2em;
}

#content .product-container .product-param .product-size {
  padding-bottom: 15px;
  margin-bottom: 15px;
  width: 100%;
  border-bottom: 1px solid #ededed;
}

#content .product-container .product-param .product-to-basket {
  margin-bottom: 15px;
}

@media (min-width: 1200px) {
  #content .product-container .product-image {
    width: 45% !important;
    height: 45% !important;
    margin: 0;
  }
  #content .product-container .product-param {
    display: inline-block;
    vertical-align: top;
    width: 45% !important;
    color: #3E3F41;
    background-color: white;
  }
  #content .product-container .product-param h1 {
    text-align: right;
  }
  #content .product-container .product-param .product-price, #content .product-container .product-param .product-to-basket, #content .product-container .product-param .product-size {
    text-align: left;
  }
  #content .product-container .product-param .brand-name, #content .product-container .product-param .product-availability {
    text-align: right;
  }
}

#content .filter-container {
  width: 100%;
  background-color: #ededed;
  color: #3E3F41;
  box-sizing: border-box;
  padding-bottom: 10px;
}

#content .filter-container input[type="text"] {
  padding: 5px;
  font-size: 1.0em;
  font-family: "Dosis", sans-serif;
  display: inline-block;
  height: 30px;
  width: 100px;
  box-sizing: border-box;
}

#content .filter-button {
  margin-left: 10px;
  margin-top: 10px;
}

footer {
  text-align: center;
  background-color: #1e1d20;
  padding: 15px;
  line-height: 100px;
  margin: 0;
}

.pagination {
