}

.product-buy-button:hover {
  cursor: pointer;
  background-color: #908A77;
}

.button-default {
  height: 40px;
  width: 200px;
  border: 1px solid #000;
  background-color: #A19B88;
  font-family: "Dosis", sans-serif;
