  background-color: #A19B88;
  font-family: "Dosis", sans-serif;
  font-size: 0.9em;
  line-height: 25px;
  display: inline-block;
  box-sizing: border-box;
  vertical-align: middle;
}

.button-small:hover {
  cursor: pointer;
  background-color: #908A77;
}

.button-cart {
  height: 25px;
  width: 75px;
  border: 1px solid #000;
  background-color: #A19B88;
