  width: 40px;
  border: 1px solid #000;
  font-family: "Dosis", sans-serif;
  text-align: center;
  box-sizing: border-box;
}

.form-classic {
  padding: 5px;
  text-align: center;
  width: 100%;
  box-sizing: border-box;
}

.form-classic input[type="text"],
.form-classic input[type="password"],
.form-classic input[type="email"],
.form-classic input[type="submit"],
.form-classic input[type="number"],
.form-classic select {
  padding: 5px;
  font-size: 1.1em;
  font-family: "Dosis", sans-serif;
  display: inline-block;
  height: 40px;
  box-sizing: border-box;
}

.form-classic input[type="text"], .form-classic input[type="password"], .form-classic input[type="email"] {
  border: 1px solid #dcdcdc;
  width: 200px;
  margin-bottom: 5px;
}

.form-classic input[type="submit"] {
  border: 1px solid #000;
  width: 150px;
  margin: 10px 0;
  background-color: #A19B88;
}

.form-classic input[type="submit"]:hover {
  cursor: pointer;
  background-color: #908A77;
}

.form-classic input[type="number"] {
  border: 1px solid #dcdcdc;
  width: 80px;
  margin-bottom: 5px;
}

.form-classic label {
  display: inline-block;
  width: 120px;
  text-transform: none !important;
  margin-top: 5px;
  padding-left: 5px;
}

.form-classic select {
  width: 200px;
  border: 1px solid #dcdcdc;
}

.form-max-width input[type="text"],
.form-max-width input[type="password"],
.form-max-width input[type="email"],
.form-max-width input[type="number"],
.form-max-width select,
.form-max-width label {
  width: 100% !important;
}

.input-file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
  text-align: center;
}

.input-file + label {
  text-align: center;
  display: inline-block;
  text-align: left;
  line-height: 25px;
  width: 125px !important;
  height: 25px;
  background-color: #A19B88;
  border: 1px solid black;
}

.input-file:focus + label, .input-file + label:hover {
  cursor: pointer;
  background-color: #908A77;
}

#form-login {
  text-align: center;
  width: 100%;
  padding: 5px;
}

#form-login input[type="text"], #form-login input[type="email"], #form-login input[type="password"] {
  width: 100%;
  margin-bottom: 10px;
}

#form-login input[type="submit"] {
  width: 200px;
}

#form-login label, #form-login i {
  font-size: 1.1em;
}

#form-login label {
  margin-left: 0;
  margin-bottom: 5px;
}

.form-search-bar input[type="text"] {
  width: 75%;
  height: 40px;
  border: 1px solid #000;
  padding: 5px;
  font-family: "Dosis", sans-serif;
  font-size: 20px;
  box-sizing: border-box;
  margin-right: 5px;
}

.form-search-bar input[type="submit"] {
  border: 1px solid #000;
  vertical-align: top;
  height: 40px;
  width: 15%;
  background-color: #A19B88;
}

.form-search-bar input[type="submit"]:hover {
  cursor: pointer;
  background-color: #908A77;
}

input[type="radio"], input[type="checkbox"] {
  display: none;
}

input[type="radio"] + label, input[type="checkbox"] + label {
  width: 100%;
}

input[type="radio"] + label span, input[type="checkbox"] + label span {
  display: inline-block;
  margin: -4px 5px 0px 0px;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  cursor: pointer;
  border: 1px solid black;
  background-color: white;
}

input[type="radio"]:checked + label span:before, input[type="checkbox"]:checked + label span:before {
  content: '';
  display: inline-block;
  height: 14px;
  width: 14px;
  margin: 1px;
  background-color: black;
}

input[type="checkbox"] + label {
  margin-right: 5px;
}

.panel-container {
